li {
  list-style: none;
  margin-bottom: 30px;
  padding: 0 10px;
}
li h4 {
  text-align: center;
  margin: 0;
}
li p {
  text-align: center;
  margin: 5px;
}
li span {
  display: block;
  text-align: center;
  margin: 5px auto;
  word-break: break-all;
}
li button {
  display: block;
  height: 25px;
  width: 60px;
  margin: 10px auto;
  background-color: transparent;
  border: rgb(65, 64, 64) solid 0.5px;
  font-weight: 700;
  cursor: pointer;
}
li button:hover {
  animation: hoverButtonEffect 0.5s both;
}
.importantCheckbox {
  transform: scale(1.3);
  cursor: pointer;
}
div button {
  height: 30px;
  width: 60px;
  margin-top: 10px;
  border: none;
  font-size: 18px;
  background-color: white;
  border-radius: 2px;
  cursor: pointer;
}
.dateInput {
  width: 180px;
  cursor: pointer;
}
h4 button {
  width: 10%;
  display: block;
  top: 35px;
  left: -110px;
  position: relative;
}
div::-webkit-scrollbar {
  width: 10px;
}
div::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #555;
}

li button {
  font-weight: 300;
}
@keyframes hoverButtonEffect {
  0% {
    background-color: transparent;
    color: black;
  }
  100% {
    background-color: black;
    color: white;
  }
}
